import { Component, OnInit } from '@angular/core';
import { Mark6Service } from '../services/mark6.service';
import { CommonService } from '../services/common.service';
import { MarkSixResult } from '../models/common.model';

@Component({
  selector: 'app-bankercal',
  templateUrl: './bankercal.component.html',
  styleUrls: ['./bankercal.component.scss']
})
export class BankerCalComponent implements OnInit {

  loaded: boolean = false;

  public m6result: MarkSixResult;

  private totalLegs: number = 0;
  totalBankerNo: number;
  totalLegNo: number;
  totalBets: number;
  amount10: number;
  amount5: number;


  bankerWinNum: number;
  legWinNum: number;
  bankerWinNoIncludeX: boolean = false;
  legWinNoIncludeX: boolean = false;


  constructor(private m6service: Mark6Service, private commonService: CommonService) { }

  ngOnInit() {
  }

  onTotalBetNoChange() {
    this.totalBets = null;
      this.amount10 = null;
      this.amount5 = null;

    if (this.totalBankerNo == null || this.totalLegNo == null) {
      return;
    }

    let sum = this.totalBankerNo + this.totalLegNo;

    if (this.totalBankerNo >= 1 && this.totalBankerNo <= 5 && this.totalLegNo >= 1 && this.totalLegNo <= 48 && (sum >= 6)) {

      this.totalBets = Math.round(this.m6service.nCr(this.totalLegNo, 6 - this.totalBankerNo));

      this.amount10 = this.totalBets * 10;
      this.amount5 = this.totalBets * 5;
    }
  }

  onCalClick() {
    this.m6result = new MarkSixResult();

    this.m6result.prize1Qty = '-';
    this.m6result.prize2Qty = '-';
    this.m6result.prize3Qty = '-';
    this.m6result.prize4Qty = '-';
    this.m6result.prize4Amount = '-';
    this.m6result.prize5Qty = '-';
    this.m6result.prize5Amount = '-';
    this.m6result.prize6Qty = '-';
    this.m6result.prize6Amount = '-';
    this.m6result.prize7Qty = '-';
    this.m6result.prize7Amount = '-';



    let results = this.m6service.calBankerLegChance(this.totalBankerNo, this.bankerWinNum, this.bankerWinNoIncludeX,
    this.totalLegNo, this.legWinNum, this.legWinNoIncludeX);

    if (results != null && results.length > 0) {

      for (let obj of results) {
        let prize = obj;

        let prizeNo = prize['PRIZE'];
        let matchCount = prize['MATCH'];

        let pNo = Number(prizeNo);

        let pCnt = Number(matchCount);

        let pNoVal = String(matchCount);

        //int nval = BONUS[(pNo - 1)];
        let pNoValx = pNo - 1;
        let nval = this.m6service.calBonusAmount(Number(pNoValx));

        let namt = nval * Number(pCnt);

        let amtStr : string = '$' + this.m6service.formatNumber(namt);

        switch (prizeNo) {
          case 1:
          this.m6result.prize1Qty = pNoVal;
          break;

          case 2:
          this.m6result.prize2Qty = pNoVal;
          break;

          case 3:
          this.m6result.prize3Qty = pNoVal;
          break;

          case 4:
          this.m6result.prize4Amount = amtStr;
          this.m6result.prize4Qty = pNoVal;
          break;

          case 5:
          this.m6result.prize5Amount = amtStr;
          this.m6result.prize5Qty = pNoVal;
          break;

          case 6:
          this.m6result.prize6Amount = amtStr;
          this.m6result.prize6Qty = pNoVal;
          break;

          case 7:
          this.m6result.prize7Amount = amtStr;
          this.m6result.prize7Qty = pNoVal;
          break;
        }
      }
    }
    this.loaded = true;
  }

  onClearClick() {
    this.totalLegs = 0;
    this.totalBankerNo = null;
    this.totalLegNo = null;

    this.bankerWinNum = 0;
    this.legWinNum = 0;
    this.bankerWinNoIncludeX = false;
    this.legWinNoIncludeX = false;
    this.totalBets = null;
    this.amount10 = null;
    this.amount5 = null;


    this.loaded = false;

    this.m6result = null;
  }
}
